.wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.container {
  width: var(--default-layout-width);
  max-width: 100%;
  padding: 0 var(--default-layout-horizontal-spacer);
  display: flex;
  margin-top: var(--default-layout-head-height);
}
.content {
  float: 1;
  margin: 0 0 0 var(--default-layout-sidebar-width);
}
