.wrapper {
  border-top: 1px solid #e3e3e4;
}
.acount-item {
  display: flex;
  padding: 8px;
}
.acount-item:hover {
  cursor: pointer;
}
.avatar {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
}
.item-info {
  flex: 1;
  margin-left: 12px;
}
.nickname {
  margin-top: 0;
  line-height: 1;
  font-size: 1.6rem;
  color: #161823;
}
.name {
  font-size: 1.2rem;
}
.check {
  font-size: 1.4rem;
  color: #20d5ec;
  margin-left: 5px;
}
.lable {
  padding: 8px;
  font-size: 1.4rem;
  font-weight: 600;
}
.seeall {
  padding: 8px;
  font-size: 1.4rem;
  font-weight: 700;
  color: #fe2c55;
  cursor: pointer;
}
