.wrapper {
  display: flex;
  align-items: center;
  cursor: pointer;
  padding: 6px 16px;
  &:hover {
    background-color: rgb(238, 234, 229);
  }
}
.avata {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}
.info {
  flex: 1;
  margin-left: 12px;
}
.name {
  font-size: 1.4rem;
  font-weight: 600;
}
.check {
  margin-left: 8px;
  font-size: 1.2rem;
  color: rgb(32, 213, 236);
}
